<template>
  <div class="decision-grail">
    <el-card>
      <div slot="header" class="card-title">
        <span>大盘分析
          <el-popover placement="right" width="300" trigger="hover">
            <div>
              <p>针对产品决策流分析。</p>
            </div>
            <i class="el-icon-question" slot="reference"></i>
          </el-popover>
        </span>
      </div>
      <span class="nodata el-table__empty-block" v-if="treeDataList.length === 0">暂无数据</span>
      <div v-for="(treeData, i) in treeDataList" :key="i">
        <div class="chartTooltip hidden" :id="`chartTooltip${i}`">
          <p v-show="unContrast" class="count"></p>
          <p v-show="unContrast" class="rate"></p>
          <table v-show="!unContrast">
            <thead>
            <tr>
              <th></th>
              <th class="contrastTitle1">时间段1&nbsp;</th>
              <th class="contrastTitle2">时间段2</th>
            </tr>
            </thead>
            <tbody>
            <tr>
              <td>总数：</td>
              <td class="contrastCount1"></td>
              <td class="contrastCount2"></td>
            </tr>
            <tr>
              <td>通过率：</td>
              <td class="contrastRate1"></td>
              <td class="contrastRate2"></td>
            </tr>
            </tbody>
          </table>
        </div>
        <svg :id="`tree-container${i}`" class="tree-container"></svg>
      </div>
    </el-card>
  </div>
</template>

<script>
import {drawTree} from '@/assets/scripts/svgTree.js';

export default {
  name: 'children-tree',
  data() {
    return {
      unContrast: !this.contractDateRange
    };
  },
  computed: {
    treeDataList() {
      return this.$store.state.treeDataList;
    },
    treeContract() {
      return this.$store.state.treeConstract;
    },
    contractDateRange() {
      return this.$store.state.contractDateRange;
    }
  },
  watch: {
    treeDataList: {
      handler: function(newV) {
        this.$nextTick(() => {
          for (let i = 0; i < newV.length; i++) {
            drawTree(
              `#tree-container${i}`,
              newV[i],
              i,
              false,
              this.treeContract,
              this.contractDateRange
            );
          }
        });
      },
      immediate: true
    }
  },
  mounted() {},
  methods: {}
};
</script>

